@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500&family=Raleway:wght@100;200;300;400;500;600&display=swap);
* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    font-family: 'Montserrat', sans-serif;
}

body {
    background-color: #1a1a1c;
    color: #ffff;
}

.App {
    max-width: 1440px;
    margin: 0 auto;
}

.coin-app {
    text-align: center;
}

.coin-search {
    margin-bottom: 64px;
    text-align: center;
}

.coin-text {
    margin-bottom: 32px;
    margin-top: 32px;
    text-align: cetner;
}

.coin-input {
    height: 50px;
    width: 300px;
    border: none;
    border-radius: 3px;
    background: linear-gradient(90deg, rgba(58, 64, 180, 1) 12%, rgba(76, 58, 180, 1) 32%, rgba(115, 52, 147, 1) 54%, rgba(52, 93, 147, 1) 70%);
    padding: 0px 10px;
    color: #ffff;
    font-size: 16px;
}

.coin-nav {
    width: 100%;
}

.coin-nav ul {
    display: flex;
    align-items: center;
    justify-content: flex-start;
}

.coin-nav ul li:first-child {
    padding: 0 24px;
    min-width: 300px;
}

.nav-child {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    width: 100%;
    text-align: end;
}

.nav-child div:first-child {
    width: 100px;
}

.nav-child div:nth-child(2) {
    width: 200px;
}

.nav-child div:nth-child(3) {
    width: 80px;
}

.nav-child div:nth-child(4) {
    width: 200px;
    padding-right: 24px;
}
.coin-container {
    display: flex;
    justify-content: center;
    align-items: center;
}

.coin-row {
    display: flex;
    align-items: center;
    justify-content: space-around;
    height: 80px;
    flex-direction: row;
    border-bottom: 1px solid #d7d7d7;
}

.coin-row__hover:hover {
    background-color: #d7d7d7;
    color: #000;
    transition: all ease .6s;
}

.coin {
    display: flex;
    align-items: center;
    padding-right: 24px;
    padding-left: 24px;
    min-width: 300px;
    justify-content: flex-start!important;
}

.coin h1 {
    font-size: 16px;
    width: 150px;
}

.coin img {
    width: 30px;
    height: 30px;
    margin-right: 10px;
}

.coin-symbol {
    text-transform: uppercase;
    font-size: 9px;
}

.coin-data {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    width: 100%;
}

.coin-price {
    width: 100px;
    text-align: end;
}

.coin-percent {
    width: 80px;
    text-align: end;
}

.coin-volume {
    width: 200px;
    text-align: end;
}

.coin-marketcap {
    width: 200px;
    padding-right: 24px;
    text-align: end;
}

.red {
    color: red;
}

.green {
    color: green;
}
